.flag-logo {
            position: relative;
            width: 60px;
            height: 40px;
            margin-right: 20px;
            transform: rotate(-8deg);
            transform-origin: center;
            animation: flutter 3s ease-in-out infinite;
        }

        .flag {
            width: 100%;
            height: 100%;
            position: relative;
            border-radius: 8px;
            overflow: hidden;
            box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
            border: 2px solid rgba(255, 255, 255, 0.8);
        }

        .flag-stripe {
            width: 100%;
            height: 33.33%;
            position: relative;
        }

        .flag-stripe.black {
            background: linear-gradient(135deg, #1a1a1a 0%, #2d2d2d 100%);
        }

        .flag-stripe.white {
            background: linear-gradient(135deg, #ffffff 0%, #f8f9fa 100%);
        }

        .flag-stripe.green {
            background: linear-gradient(135deg, #28a745 0%, #20c997 100%);
        }

        .flag-triangle {
    position: absolute;
    left: 0;
    top: 0;
    width: 0;
    height: 0;
    border-style: solid;
    /* Adjusting the triangle to fit better for the Palestinian flag */
    border-width: 18px 0 25px 23px;
    border-color: transparent transparent transparent #d93f3b; /* Adjusted red color */
    background: linear-gradient(135deg, #d93f3b 0%, #c0392b 100%); /* Darker red gradient */
    z-index: 10;
}

.flag-triangle::before {
    content: '';
    position: absolute;
    left: -40px;
    top: -25px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 25px 0 25px 40px;
    border-color: transparent transparent transparent #c0392b; /* Darker red color */
}
        .flag-glow {
            position: absolute;
            top: -5px;
            left: -5px;
            right: -5px;
            bottom: -5px;
            background: linear-gradient(45deg, #dc3545, #28a745, #ffffff, #1a1a1a);
            border-radius: 12px;
            opacity: 0;
            animation: glow 4s ease-in-out infinite;
            z-index: -1;
        }
        @keyframes flutter {
            0%, 100% {
                transform: rotate(-18deg) scale(1);
            }
            25% {
                transform: rotate(-16deg) scale(1.02) translateY(-2px);
            }
            50% {
                transform: rotate(16deg) scale(0.98) translateY(1px);
            }
            75% {
                transform: rotate(18deg) scale(1.01) translateY(-1px);
            }
        }
        @media(max-width:720px){
            .flag-logo{
                height: 40px;
                width: 55px;
            }
            .flag-triangle{
                border-width: 19px 0px 10px 25px;
            }
        }
        @media(max-width:400px){
            .flag-logo{
                height: 30px;
                width: 40px;
            }
            .flag-triangle{
                border-width: 14px 0px 5px 18px;
            }
        }
        @media(max-width:385px){
            .flag-logo{
                height: 28px;
                width: 30px;
            }
            .flag-triangle{
                border-width: 13px 0px 7px 14px;
            }
        }